import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import "./App.css";
import Search from "./Search";
import Highscores from "./Highscores";

// Initialize Firebase

import * as firebase from "firebase/app";
import "firebase/firestore";

const firebaseConfig = {
  apiKey: "AIzaSyDEY3VXlvcsjUFBFxa3H3RZYexpacQ4Xmc",
  authDomain: "findwaldo-5eb83.firebaseapp.com",
  projectId: "findwaldo-5eb83",
  storageBucket: "findwaldo-5eb83.appspot.com",
  messagingSenderId: "613558348209",
  appId: "1:613558348209:web:85c6d8794f1aac96bbf590",
};

firebase.initializeApp(firebaseConfig);

var db = firebase.firestore();

const App = () => {
  return (
    <div>
      <Router>
        <Switch>
          <Route exact path="/waldojohn" render={() => <Search db={db} />} />

          <Route
            exact
            path="/highscores"
            render={() => <Highscores db={db} />}
          />
        </Switch>
      </Router>
    </div>
  );
};

export default App;
